<template>
  <div class="hello">
    <div class="top_div dsp_flex flex_lvCenter">
      <div class="top_inner_div">
        <!-- 区域定位 -->
        <div class="jobArea_div">
          <div class="dsp_flex flex_verticalCenter" @click="jobAreaClkFn">
            <span class="cityName_span">{{cityName | cityNameFilter}}</span>
            <img :src="arrowImgPath" alt="">
          </div>
        </div>
      </div>
    </div>
    <div class="jobList_div middle_div">
      <div class="list_banner_div dsp_flex flex_lvCenter  ">
        <img v-for="(item,index) in hotJobBgPathArr" :key="index" :src="item.path" />
      </div>
      <div class="list_div_wrapper">
        <div class="list_div" v-for="(item,index) in jobDataArr" :key="index">
          <div class="jobMiniCardImg" v-show="item.abbreviation">
            <span>{{item.abbreviation}}</span>
          </div>
          <p class="jobBrief_p" :class="{'mb_34' : !item.title}" v-html="item.introduction" v-show="item.introduction"></p>
          <div class="fBtn" v-show="item.title">{{item.title}}</div>
          <div class="jobDetail_div dsp_flex flex_verticalCenter" v-show="item.totalsalary">
            <img :src="moneyPath" alt="" />
            <span class="label_txt">综合薪资 :</span>
            <span class="cnt_txt">{{item.totalsalary}}</span>
          </div>
          <div class="jobDetail_div dsp_flex flex_verticalCenter mt-24" v-show="item.workCityText || item.workDistText">
            <img :src="locationPath" alt="" />
            <span class="label_txt">工作地点 :</span>
            <span class="cnt_txt">{{item.workCityText}}-{{item.workDistText}}</span>
          </div>
          <div class="jobDetail_div dsp_flex flex_verticalCenter mt-24" v-show="item.jobLabel">
            <img :src="welfarePath" alt="" />
            <span class="label_txt">员工福利 :</span>
            <span class="cnt_txt c-1c70d3">{{item.jobLabel | jobLabelFilter}}</span>
          </div>
          <mt-button class="regBtn" :class="{'hasReg': item.isApply === 1}" type="default" :disabled="item.isApply == 1"  @click="jobSignUpBtn(item.jobBaseId)">{{item.isApply == 1 ? '已报名' : '立即报名'}}</mt-button>
        </div>
      </div>
    </div>
    <div class="bottom_div">
      <div class="bottom_banner_div dsp_flex flex_lvCenter  ">
        <img v-for="(item,index) in jobEnvBgPathArr" :key="index" :src="item.path" />
      </div>
      <div class="jobEnv_div"></div>
      <div class="workplace_div dsp_flex flex_lvCenter">
        <img :src="workplaceImgPath" alt="">
      </div>
      <div class="workplaceImg_div"></div>
    </div>
    <!-- 右侧区域弹框 -->
    <mt-popup position="right"
      v-model="popupVisible"
      class="popup_div"
    >
      <mt-index-list>
        <mt-index-section :index="item.title" v-for="(item,index) in cityjson" :key="index">
          <mt-cell :title="i.name" v-for="(i,index) in item.lists" :key="index" @click.native="cityCellClkFn(i.name)"></mt-cell>
        </mt-index-section>
      </mt-index-list>
    </mt-popup>
    <!-- modal -->
    <div class="modal_div" v-show="modalStatus">
      <div class="modal_div_inner" :class="{'modalHeight1' : hasReg }">
        <img class="regModalClose_icon" :src="closeIconPath" alt="" v-show="modalCloseShow" @click="modalStatus = false, loginTel = '', smsCodeVal = '', authBtnTxt = '获取验证码'">
        <!-- 注册 -->
        <div class="reg_modalInner" v-show="regModalStatus">
          <input class="tel_ipt" type="tel" placeholder="请输入手机号码" maxlength="11" v-model="loginTel" />
          <div class="dsp_flex getAuth_div">
            <input type="tel" class="authCode_ipt" placeholder="请输入短信验证码" v-model="smsCodeVal" />
            <mt-button class="getAuthBtn" type="default" @click.native="getAuthBtnFn" :disabled="getAuthBtnStatus">{{authBtnTxt}}</mt-button>
          </div>
          <mt-button class="ensureBtn" type="default" @click="loginBtnClkFn">确定</mt-button>
        </div>
        <!-- 已经有账号登录 -->
        <div class="hasReg_modalInner" v-show="hasRegModalStatus">
          <p>检测到您已有账号为</p>
          <p>{{hasRegTel}}</p>
          <div class="btn_wraper dsp_flex flex_levelCenter">
            <mt-button class="hasReg_btn" type="default" @click="hasRegModalStatus = false, regModalStatus = true">切换账号</mt-button>
            <mt-button class="hasReg_btn" type="default" @click="modalStatus = false">快捷登录</mt-button>
          </div>
        </div>
        <!-- 报名成功 -->
        <div class="signUp_modalInner" v-show="signUpModalStatus">
          <p>您已报名成功，请保持电话畅通</p>
          <p>稍后将有工作人员与您取得联系</p>
          <mt-button class="signUp_btn" type="default">{{signUpSecond}}秒后自动关闭</mt-button>
        </div>
        <!-- 人机校验 -->
        <div class="checkMan_modalInner" v-show="checkManModalStatus">
          <p>请输入验证码</p>
          <div class="yzm_div dsp_flex">
            <input type="text" placeholder="请输入图形验证码" v-model="yzmVal" />
            <img :src="yzmImgPath" alt="" @click="resetYzmClkFn" />
          </div>
          <div class="btn_wraper dsp_flex flex_levelCenter">
            <mt-button class="hasReg_btn" type="default" @click="checkManModalStatus = false, regModalStatus = true, modalCloseShow = true">取消</mt-button>
            <mt-button class="hasReg_btn" type="default" @click="yzmEnsureBtn">确定</mt-button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
// import cityJson from '../assets/json/city.json'
import { Toast, Indicator } from 'mint-ui'
// import Qs from 'qs'

let _this

function ToastFn (msg) {
  Toast({
    message: msg,
    position: 'bottom',
    duration: 5000
  })
}

export default {
  name: 'HelloWorld',
  data () {
    return {
      popupVisible: false,
      // cityArr: cityJson.city,
      cityArr2: ['上海', '北京', '湖南', '天津', '重庆', '东京', '安阳', '阜阳', '阿坝', '阿拉善', '阿里', '安康', '安庆', '鞍山'],
      FristPin: ['A', 'B', 'C', 'D', 'E', 'F', 'G', 'H', 'J', 'K', 'L', 'M', 'N', 'P', 'Q', 'R', 'S', 'T', 'W', 'X', 'Y', 'Z'],
      cityjson: [],
      cityName: '',
      authBtnTxt: '获取验证码',
      getAuthBtnStatus: false,
      arrowImgPath: require('../assets/img/arrow.png'),
      hotJobBgPathArr: [
        {
          path: require('../assets/img/hotJob_ch.png')
        }, {
          path: require('../assets/img/hotJob_en.png')
        }
      ],
      moneyPath: require('../assets/img/qian.png'),
      locationPath: require('../assets/img/didian.png'),
      welfarePath: require('../assets/img/fuli.png'),
      jobEnvBgPathArr: [
        {
          path: require('../assets/img/jobEnv_ch.png')
        }, {
          path: require('../assets/img/jobEnv_en.png')
        }
      ],
      workplaceImgPath: require('../assets/img/workplace_word.png'),
      closeIconPath: require('../assets/img/close_icon.png'),
      hasReg: false,
      regModalStatus: true,
      hasRegModalStatus: false,
      signUpModalStatus: false,
      modalStatus: true,
      jobDataArr: [],
      cityNameListArr: [],
      loginTel: '',
      modalCloseShow: true,
      yzmImgPath: '',
      checkManModalStatus: false,
      yzmVal: '',
      smsCodeVal: '',
      hasRegTel: '',
      signUpSecond: 3
    }
  },
  created () {
    _this = this
  },
  mounted () {
    if (this.$route.query.cityName) {
      this.cityName = this.$route.query.cityName
      this.getCityJobData()
    } else {
      this.getLocationInfo()
    }
    // 判断是否登录
    this.whetherLoginFn()
  },
  filters: {
    cityNameFilter (data) {
      if (data.indexOf('市') === 1) {
        return data.substr(0, data.length - 1)
      } else {
        return data
      }
    },
    jobLabelFilter (data) {
      if (data) {
        return data.replace(/,/g, ' ')
      }
    }
  },
  methods: {
    // 获取位置信息
    getLocationInfo () {
      // key OB4BZ-D4W3U-B7VVO-4PJWW-6TKDJ-WPB77  JKRBZ-4OTKU-TZCVV-BLZXP-U5PPO-PPFQ7
      var geolocation = new qq.maps.Geolocation('JKRBZ-4OTKU-TZCVV-BLZXP-U5PPO-PPFQ7', 'myapp')
      var options = { timeout: 8000, failTipFlag: true }
      // let instance = Toast({
      //   message: '定位中...',
      //   position: 'bottom',
      //   duration: 8000
      // })
      ToastFn('定位中...')

      function showPosition (position) {
        if (Object.keys(position).length !== 0) {
          _this.cityName = position.city.substr(0, position.city.length - 1)
          ToastFn('定位成功,当前' + _this.cityName)
          // instance.close()
          _this.getCityJobData()
        }
      }

      function showErr () {
        ToastFn('定位失败,默认苏州')
        _this.cityName = '苏州'
        _this.getCityJobData()
      }
      geolocation.getLocation(showPosition, showErr, options)
    },
    // 城市点击
    jobAreaClkFn () {
      this.popupVisible = true
      if (this.cityNameListArr.length === 0) {
        Indicator.open()
      }
    },
    // 获取城市职位
    getCityJobData () {
      let $token = window.sessionStorage.getItem('token') ? window.sessionStorage.getItem('token') : ''
      this.$http.get('api/activity/activityByCityName', {
        params: {
          cityName: this.cityName,
          token: $token
        }
      }).then(res => {
        let { code, data } = res.data
        if (code === 10000) {
          this.jobDataArr = data.activityJobBaseVoList
          this.cityNameListArr = data.cityNameList
          if (this.cityNameListArr.length > 0) {
            Indicator.close()
          }
          this.cityNameFormatFn()
        }
      })
    },
    // 城市排序
    cityNameFormatFn () {
      // 区域排序
      let pinyin = require('js-pinyin')
      pinyin.setOptions({checkPolyphone: false, charCase: 0})
      let cityArr = []
      this.cityNameListArr.forEach(item => {
        let cityObj = {}
        let cityName = item
        let cityFristLetter = pinyin.getFullChars(cityName).substring(0, 1) // 这里截取首字母的第一位
        cityObj.title = cityFristLetter
        cityObj.name = item
        cityArr.push(cityObj)
      })
      var demoCityArr = []
      this.FristPin.forEach(item => {
        let cityJson = {}
        cityJson['lists'] = cityArr.filter(value => {
          return value.title === item
        })
        cityJson['title'] = item
        demoCityArr.push(cityJson)
      })
      this.cityjson = demoCityArr
    },
    // 获取验证码倒计时
    getAuthBtnFn () {
      if (!this.loginTel) {
        ToastFn('请填写手机号码')
        return false
      } else {
        if (!(/^1[3456789]\d{9}$/.test(this.loginTel))) {
          ToastFn('手机号码不正确')
          return false
        }
      }
      this.checkManModalStatus = true
      this.regModalStatus = false
      this.modalCloseShow = false
      this.yzmVal = ''
      this.resetYzmClkFn()
    },
    // 右侧城市选择
    cityCellClkFn (data) {
      this.cityName = data
      this.popupVisible = false
      this.getCityJobData()
    },
    // 登录确定
    loginBtnClkFn () {
      if (!this.loginTel) {
        ToastFn('请填写手机号码')
        return false
      } else {
        if (!(/^1[3456789]\d{9}$/.test(this.loginTel))) {
          ToastFn('手机号码不正确')
          return false
        }
      }
      if (this.smsCodeVal) {
        this.$http.post(`api/activity/shortMessageLogin?mobile=${this.loginTel}&code=${this.smsCodeVal}`).then(res => {
          // let data = {
          //   mobile: this.loginTel,
          //   code: this.smsCodeVal
          // }
          // this.$http.post(`api/activity/shortMessageLogin`, {
          //   params: {
          //     mobile: this.loginTel,
          //     code: this.smsCodeVal
          //   }
          // }).then(res => {
          let { code, msg, data } = res.data
          if (code === 10000) {
            ToastFn('登录成功')
            window.sessionStorage.setItem('token', data)
            this.modalStatus = false
            this.getCityJobData()
          } else {
            ToastFn(msg)
          }
        })
      } else {
        ToastFn('请输入短信验证码')
        return false
      }
    },
    // 更换验证码
    resetYzmClkFn () {
      this.$http.get('api/activity/getCaptcha', {
        params: {
          mobile: this.loginTel
        },
        responseType: 'blob'
      }).then(res => {
        let blob = new Blob([res.data], {
          type: 'image/png'
        })
        let url = window.URL.createObjectURL(blob)
        this.yzmImgPath = url
      })
    },
    // 立即报名
    jobSignUpBtn (id) {
      let $token = window.sessionStorage.getItem('token') // 回调可简写
      this.$http.get('api/activity/isToken', {
        params: {
          token: $token
        }
      }).then(res => {
        let { code } = res.data
        if (code === 10000) {
          this.$http.get('api/activity/activityapply', {
            params: {
              jobBaseId: id,
              token: $token
            }
          }).then(res => {
            let { code, msg } = res.data
            if (code === 10000) {
              this.modalStatus = true
              this.signUpModalStatus = true
              this.regModalStatus = false
              this.hasRegModalStatus = false
              this.checkManModalStatus = false

              var count = 3
              var timer = setInterval(() => {
                count--
                _this.signUpSecond = count
                if (count === 0) {
                  clearInterval(timer)
                  _this.modalStatus = false
                }
              }, 1000)
              this.getCityJobData()
            } else {
              Toast(msg)
            }
          })
        } else {
          this.modalStatus = true
          this.regModalStatus = true
        }
      })
    },
    // 验证码弹框确定
    yzmEnsureBtn () {
      if (this.yzmVal) {
        this.$http.get('api/activity/sendSmsCode', {
          params: {
            mobile: this.loginTel,
            MachineCodeID: 'suibianla',
            code: this.yzmVal
          }
        }).then(res => {
          let { code, msg } = res.data
          if (code === 10000) {
            var count = 60
            var timer = setInterval(() => {
              count--
              _this.authBtnTxt = count + 's'
              _this.getAuthBtnStatus = true
              if (count === 0) {
                _this.authBtnTxt = '获取验证码'
                _this.getAuthBtnStatus = false
                clearInterval(timer)
              }
            }, 1000)
            ToastFn('短信发送成功')
            this.checkManModalStatus = false
            this.regModalStatus = true
            this.modalCloseShow = true
          } else {
            ToastFn(msg)
          }
        })
      } else {
        ToastFn('请输入图形验证码')
      }
    },
    // 判断是否登录
    whetherLoginFn () {
      let $token = window.sessionStorage.getItem('token')
      this.$http.get('api/activity/isToken', {
        params: {
          token: $token
        }
      }).then(res => {
        let { code, data } = res.data
        if (code === 10000) {
          this.modalStatus = true
          this.hasRegModalStatus = true
          this.regModalStatus = false
          this.hasRegTel = data
        } else {
          this.modalStatus = true
          this.regModalStatus = true
        }
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



